// Call plugin
import { callActions } from '../../../interface/actions'
import { getCallById, getOptions } from '../../../interface/selectors'
import { CALL_STATES, CALL_DIRECTION } from '../../../constants'
import { CALL_STATE_CHANGE } from '../../../interface/eventTypes'

// Other plugins
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export function validate (callId, call, options) {
  /*
   * `sendRingingFeedback` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Initiated` state.
   *    3. Call must be `Incoming` direction.
   */
  if (!call) {
    return new BasicError({
      message: `Failed to send ringing feedback. Call ${callId} not found.`,
      code: callCodes.INVALID_PARAM
    })
  } else if (options.ringingFeedbackMode !== 'manual') {
    return new BasicError({
      message: "Failed to send ringing feedback. Configuration must be set to 'manual' mode.",
      code: callCodes.NOT_SUPPORTED
    })
  } else if (call.state !== CALL_STATES.INITIATED) {
    return new BasicError({
      message: `Failed to send ringing feedback. Call must be in ${CALL_STATES.INITIATED} state.`,
      code: callCodes.INVALID_STATE
    })
  } else if (call.direction !== CALL_DIRECTION.INCOMING) {
    return new BasicError({
      message: 'Failed to send ringing feedback. Call must be incoming.',
      code: callCodes.INVALID_STATE
    })
  }
}

/**
 * Bottle wrapper for sendRingingFeedback call operation.
 * @return {Function}
 */
export default function sendRingingFeedbackOperation (container) {
  const { context, CallRequests, emitEvent, logManager } = container
  /**
   * Sends ringing feedback to the server for an incoming, Initiated call.
   *
   * This saga defines how/when a call can send ringing feedback to the remote user.
   *    It performs signaling to send this notification to the server, which forwards
   *    it to the remote side of the call.
   * Assumptions:
   *    1. The call is in Initiated state.
   *    2. The call's direction is incoming.
   *    3. The SDK is configured `ringingFeedbackMode: 'manual'`.
   * Responsibilities:
   *    1. Perform signaling.
   *    2. Update call state (via redux action).
   * @method sendRingingFeedback
   * @param {string}   callId        The id for the call sending ringing feedback.
   */
  async function sendRingingFeedback (callId) {
    const log = logManager.getLogger('CALL', callId)
    log.info('Sending ringing feedback for incoming call.')

    const options = getOptions(context.getState())
    const targetCall = getCallById(context.getState(), callId)

    const validationError = validate(callId, targetCall, options)
    if (validationError) {
      log.info(validationError.message)
      context.dispatch(callActions.sendRingingFeedbackFinish(callId, { error: validationError }))

      if (targetCall) {
        emitEvent(CALL_STATE_CHANGE, {
          callId,
          previous: {
            state: targetCall.state,
            localHold: targetCall.localHold,
            remoteHold: targetCall.remoteHold
          },
          error: validationError
        })
      }

      throw validationError
    }

    const callInfo = { wrtcsSessionId: targetCall.wrtcsSessionId, id: targetCall.id }
    try {
      await CallRequests.updateCallRinging(callInfo)
    } catch (error) {
      log.info(`Failed to send ringing feedback: ${error.code}: ${error.message}`)
      context.dispatch(callActions.sendRingingFeedbackFinish(targetCall.id, { error }))

      emitEvent(CALL_STATE_CHANGE, {
        callId,
        previous: {
          state: targetCall.state,
          localHold: targetCall.localHold,
          remoteHold: targetCall.remoteHold
        },
        error
      })

      throw error
    }

    log.info(`Ringing feedback sent. Changing call to ${CALL_STATES.RINGING} state.`)
    context.dispatch(callActions.sendRingingFeedbackFinish(targetCall.id))

    emitEvent(CALL_STATE_CHANGE, {
      callId,
      previous: {
        state: targetCall.state,
        localHold: targetCall.localHold,
        remoteHold: targetCall.remoteHold
      }
    })
  }

  return sendRingingFeedback
}
