// Call plugin
import { callActions } from '../../../interface/actions'
import { CALL_STATES, CALL_DIRECTION } from '../../../constants'
import { getCallById } from '../../../interface/selectors'
import { CALL_STATE_CHANGE } from '../../../interface/eventTypes'

// Other plugins
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export function validate (callId, call) {
  /*
   * `ignore` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Initiated` or `Ringing` state.
   *    3. Call must be `Incoming` direction.
   */
  if (!call) {
    // Call must exist.
    return new BasicError({
      message: `Failed to ignore call. Call ${callId} not found.`,
      code: callCodes.INVALID_PARAM
    })
  } else if (![CALL_STATES.INITIATED, CALL_STATES.RINGING].includes(call.state)) {
    // Call must be in Initiated or Ringing state.
    return new BasicError({
      message: `Failed to ignore call. Call must be in ${CALL_STATES.INITIATED} or ${CALL_STATES.RINGING} state.`,
      code: callCodes.INVALID_STATE
    })
  } else if (call.direction !== CALL_DIRECTION.INCOMING) {
    // Call must be incoming.
    return new BasicError({
      message: 'Failed to ignore call. Call must be incoming.',
      code: callCodes.INVALID_STATE
    })
  }
}

/**
 * Bottle wrapper for ignore call operation.
 * @return {Function}
 */
export default function ignoreOperation (container) {
  const { context, CallstackWebrtc, emitEvent, logManager } = container
  /**
   * Ignores an incoming call.
   *
   * This operation defines how a call is ignored. It performs the webRTC
   *    operations to ignore a "call".
   * Assumptions:
   *    1. None
   * Responsibilities:
   *    1. Cleanup the call locally, using the webRTC helper operation.
   *    2. Update call state (via redux action).
   * @method ignoreCall
   * @param {string}   callId        The id for the call to be ignored.
   */
  async function ignore (callId) {
    const log = logManager.getLogger('CALL', callId)
    log.info('Ignoring incoming call.')

    const targetCall = getCallById(context.getState(), callId)

    const validationError = validate(callId, targetCall)
    if (validationError) {
      log.info(validationError.message)

      context.dispatch(callActions.ignoreCallFinish(callId, { error: validationError }))

      let previous
      if (targetCall) {
        previous = {
          state: targetCall.state,
          localHold: targetCall.localHold,
          remoteHold: targetCall.remoteHold
        }
      } else {
        previous = {
          state: CALL_STATES.RINGING,
          localHold: false,
          remoteHold: false
        }
      }
      emitEvent(CALL_STATE_CHANGE, {
        callId,
        previous,
        error: validationError
      })

      throw validationError
    }

    // Clean-up webRTC objects. Ignore any errors returned, since we want the
    //    call / session to be ended either way.
    CallstackWebrtc.closeCall(targetCall.webrtcSessionId)

    log.info(`Finished ignoring call. Changing to ${CALL_STATES.ENDED}.`)
    // Report the operation complete.
    context.dispatch(callActions.ignoreCallFinish(targetCall.id))

    // Update the call state
    emitEvent(CALL_STATE_CHANGE, {
      callId,
      previous: {
        state: targetCall.state,
        localHold: targetCall.localHold,
        remoteHold: targetCall.remoteHold
      }
    })
  }

  return ignore
}
