// Call plugin
import { callActions } from '../../../interface/actions'
import * as eventTypes from '../../../interface/eventTypes'
import { getCallById } from '../../../interface/selectors'
import { CALL_STATES, CALL_DIRECTION } from '../../../constants'

// Other plugins
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export function validate (callId, call) {
  /*
   * `reject` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Initiated` or `Ringing` state.
   *    3. Call must be `Incoming` direction.
   */
  if (!call) {
    // Call must exist.
    return new BasicError({
      message: `Failed to reject call. Call ${callId} not found.`,
      code: callCodes.INVALID_PARAM
    })
  } else if (![CALL_STATES.INITIATED, CALL_STATES.RINGING].includes(call.state)) {
    // Call must be in Initiated or Ringing state.
    return new BasicError({
      message: `Failed to reject call. Call must be in ${CALL_STATES.INITIATED} or ${CALL_STATES.RINGING} state.`,
      code: callCodes.INVALID_STATE
    })
  } else if (call.direction !== CALL_DIRECTION.INCOMING) {
    // Call must be incoming.
    return new BasicError({
      message: 'Failed to reject call. Call must be incoming.',
      code: callCodes.INVALID_STATE
    })
  }
}

/**
 * Bottle wrapper for reject call operation.
 * @return {Function}
 */
export default function rejectOperation (container) {
  const { context, CallRequests, CallstackWebrtc, emitEvent, logManager } = container
  /**
   * Rejects an incoming call.
   *
   * This operation defines how a call is rejected. It performs the WebRTC and
   *    signalling operations to reject a "call" both locally and on the server.
   * Assumptions:
   *    1. None
   * Responsibilities:
   *    1. Cleanup the call locally, using the WebRTC helper saga.
   *    2. Reject the call on the server.
   *    3. Update call state (via redux action).
   * @method rejectCall
   * @param {Object} callId The ID of the call to reject.
   */
  async function reject (callId) {
    const log = logManager.getLogger('CALL', callId)
    log.info('Rejecting incoming call.')

    const incomingCall = getCallById(context.getState(), callId)
    const validationError = validate(callId, incomingCall)
    if (validationError) {
      log.info(validationError.message)
      context.dispatch(callActions.rejectCallFinish(callId, { error: validationError }))

      throw validationError
    }

    await CallstackWebrtc.closeCall(incomingCall.webrtcSessionId)

    // Collect the information needed to make the request.
    const callInfo = {
      id: callId,
      wrtcsSessionId: incomingCall.wrtcsSessionId
    }
    try {
      await CallRequests.rejectSession(callInfo)
    } catch (error) {
      log.info('Failed to reject call.')
      context.dispatch(callActions.rejectCallFinish(callId, { error }))

      emitEvent(eventTypes.CALL_STATE_CHANGE, {
        callId,
        previous: {
          state: incomingCall.state,
          localHold: incomingCall.localHold,
          remoteHold: incomingCall.remoteHold
        }
      })
      throw error
    }

    log.info(`Finished rejecting call. Changing to ${CALL_STATES.ENDED}.`)
    context.dispatch(callActions.rejectCallFinish(callId))
    emitEvent(eventTypes.CALL_STATE_CHANGE, {
      callId,
      previous: {
        state: incomingCall.state,
        localHold: incomingCall.localHold,
        remoteHold: incomingCall.remoteHold
      }
    })
  }

  return reject
}
