// Call plugin.
import { getLocalTracks } from '../../utils/call'
import { getCallById } from '../../../interface/selectors'
import { callActions } from '../../../interface/actions'
import { CALL_STATES } from '../../../constants'

// Other plugins
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export function validate (call, tracks, kind) {
  /*
   * `addMedia` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Connected` state.
   * `addBasicMedia` operation validation:
   *    3. Call must not already have a track of `kind` media.
   */
  if (!call) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: 'Call state not found; invalid call ID.'
    })
  } else if (call.state !== CALL_STATES.CONNECTED) {
    return new BasicError({
      code: callCodes.INVALID_STATE,
      message: `Invalid call state: ${call.state}. It should be: ${CALL_STATES.CONNECTED}.`
    })
  } else if (tracks.length >= 1) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: `Too many ${kind} tracks for basic scenario!`
    })
  }
}

/**
 * Bottle wrapper for addBasicMedia call operation.
 * @return {Function}
 */
export default function addBasicMediaOperation (container) {
  const { context, logManager } = container
  /**
   * Adds basic media scenario to the call
   *
   * This operation wraps addMedia as an easier to use function for simple scenarios
   * Assumptions:
   *    1. Validate that the call state is "Connected".
   *        - The call is connected.
   *    2. The track being added cannot already be on the call.
   * Responsibilities:
   *    1. Check that there isn't already a track of the same kind on the call
   *    2. Call addMedia with the correct parameters
   * @method addBasicMedia
   * @param {string}                  id               Id for the call being operated on.
   * @param {string}                  kind             The type of media being added.
   * @param {Object}                  mediaConstraints Parameters describing the media being added.
   * @param {Object}                 [options]
   * @param {call.BandwidthControls} [options.bandwidth] Options for configuring media's bandwidth.
   * @param {call.DSCPControls}      [options.dscpControls] Options for configuring DSCP markings on the media traffic.
   */
  async function addBasicMedia (id, kind, mediaConstraints, options) {
    const log = logManager.getLogger('CALL', id)
    log.debug(`Adding ${kind} media to call.`)

    const call = getCallById(context.getState(), id)
    const tracks = getLocalTracks(context, id, kind)
    const validationError = validate(call, tracks, kind)
    if (validationError) {
      log.debug(validationError.message)
      context.dispatch(
        callActions.addMediaFinish(id, {
          local: true,
          error: validationError
        })
      )

      throw validationError
    }

    const { Callstack } = container
    await Callstack.operations.addMedia(id, mediaConstraints, options)
  }

  return addBasicMedia
}
