// Operations.
import holdOperation from './hold'
import rollbackOperation from './rollbackHold'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.hold', () => holdOperation(bottle.container))

  bottle.factory('Callstack.utils.rollbackHold', () => rollbackOperation(bottle.container))
}
