// Call plugin
import { getCallById } from '../../../interface/selectors'
import { callActions } from '../../../interface/actions'
import { CALL_STATES } from '../../../constants'

// Other plugins
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export function validate (callId, call) {
  /*
   * `sendCustomParameters` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Connected` or `On Hold` state.
   */
  if (!call) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: `Invalid Call ID. No call found with Call ID: ${callId}`
    })
  } else if (call.state !== CALL_STATES.CONNECTED && call.state !== CALL_STATES.ON_HOLD) {
    return new BasicError({
      code: callCodes.INVALID_STATE,
      message: `Call in invalid state for sending custom parameters. Call state: ${call.state}`
    })
  }
}

/**
 * Bottle wrapper for sendCustomParameters call operation.
 * @return {Function}
 */
export default function sendCustomParametersOperation (container) {
  const { context, CallRequests, logManager } = container

  /**
   * Sends custom parameters for a call.
   * @param {string} callId The ID of the call being acted on.
   * @return {undefined}
   * @throws {BasicError} Throws an error if the REST request fails.
   */
  async function sendCustomParameters (callId) {
    const log = logManager.getLogger('CALL', callId)
    log.info('Sending custom parameters for call.')

    // Get the call.
    const targetCall = getCallById(context.getState(), callId)

    const validationError = validate(callId, targetCall)
    if (validationError) {
      log.debug(validationError.message)

      context.dispatch(callActions.sendCustomParametersFinish(callId, { error: validationError }))

      throw validationError
    }

    const { wrtcsSessionId, isAnonymous, account, customParameters } = targetCall

    const callInfo = {
      wrtcsSessionId,
      isAnonymous,
      account,
      customParameters
    }

    try {
      await CallRequests.updateCustomParameters(callInfo)

      log.info('Finished sending custom parameters.')
      context.dispatch(
        callActions.sendCustomParametersFinish(callId, {
          error: false
        })
      )
    } catch (error) {
      log.info('Failed to send custom parameters.')
      context.dispatch(callActions.sendCustomParametersFinish(callId, { error }))
    }
  }

  return sendCustomParameters
}
